<style lang='scss' rel="stylesheet/scss">
    @import "../../../utils/mixins/mixins";
    @import "../../../utils/mixins/table";
    @import "../../../utils/mixins/common";
    @import "../../../utils/mixins/topSearch";

    .block-manage {
        @extend %justify;
        .content-title {
            padding: 10px 20px;
            background: #f0f3f5;
            text-align: left;
            line-height: 35px;
            button {
                float: right;
                display: block;
                margin-left: 15px;
            }
        }
        .left-content {
            display: inline-block;
            vertical-align: top;
            width: 25%;
            background: #fff;
            .classify-tree {
                padding: 1px;
                // .el-tree-node{
                //     overflow: hidden;
                // }
            }
        }
        .right-content {
            width: 74%;
            display: inline-block;
            vertical-align: top;
            background: #fff;
            padding-bottom: 20px;
            .content-list {
                padding: 20px;
                .search{
                    width:100%;
                    margin-bottom:10px;
                    section{
                        margin-left: 10px;
                        margin-bottom: 14px;
                        display: inline-block;
                        margin-right: 10px;
                        .el-select .el-input .el-input__icon{
                            transform: translateY(-50%) translateX(-50%)rotateZ(180deg);
                        }
                        #input{
                            width: 84%;
                            display: inline-block;
                            margin-left: 10px;
                        }
                    }
                    .fi{
                        width:35%;
                    }
                }
                .tag {
                    span {
                        padding: 10px;
                        border: 1px solid #e2e7eb;
                        background: #fff;
                        border-right: none;
                        &:last-of-type {
                            border-right: 1px solid #e2e7eb;
                        }
                        &:hover {
                            background: #e2e7eb;
                        }
                        &.active {
                            background: #e2e7eb;
                        }
                    }
                }
            }
            .el-pagination {
                float: right;
                margin-top: 10px;
            }
        }
    }
</style>
<template>
    <article class="block-manage">
        <section class="left-content">
            <div class="content-title">
                
                <router-link tag="el-button" :to="{name: 'course-category-manage'}"></router-link>
            </div>
            <div class="classify-tree">
                <CourseCategoryTree v-model="treeData" ref="courseCategory" :onNodeClick="treeNodeClick.bind(this,1)"></CourseCategoryTree>
                 <!-- <MenuTree :data="SecMenu" v-if="SecMenu.length" ref="chapterCategory" :Mult='Mult'></MenuTree> -->
            </div>
        </section>
        <section class="right-content">
            <div class="content-title">
                <span v-if="category.title">{{category.title}}-</span>
                <!-- <el-button type="primary" icon="plus" @click="$router.push({ name:'course-manage-addCourse'})"><i></i></el-button>
                <el-button type="warning" icon="plus" @click="addHerbal"><i></i> </el-button> -->
                <el-button type="primary" icon="plus" @click="addCourse"><i></i></el-button>
            </div>
            <div class="content-list">
                <div class="search">
                    <section class="fi">
                     <i></i><el-input id="input" v-model="section.course_name" placeholder="" @keyup.enter.native="fetchCourseLists" auto-complete="off" ></el-input>
                    </section>  
                    <DateRange title="" :start="section.create_start" :end="section.create_end" @changeStart="val=> section.create_start =val "
                        @changeEnd="val=> section.create_end=val" :change="fetchCourseLists">
                    </DateRange>
                    <section>
                        <i></i>
                        <el-select v-model="section.status" placeholder="" @change="fetchCourseLists" :clearable="true">
                            <el-option label="" value="-1"></el-option>
                            <el-option label="" value="0"></el-option>
                            <el-option label=" " value="1"></el-option>
                            <el-option label="" value="2"></el-option>
                        </el-select>
                    </section>
                    <section><i></i>
                        <CourseCategorySelect :onchange="fetchCourseLists" v-model="section.category_id"></CourseCategorySelect>
                    </section> 
                </div>     
                <el-table v-loading="section.loading" border :data="section.data">
                    <el-table-column prop="course_name" label="" min-width="230"></el-table-column>
                    <el-table-column prop="category_name" label="" width="190">
                        <!-- <template scope="scope">
                            {{scope.row.category_name || ''}}
                        </template> -->
                    </el-table-column>
                    <el-table-column prop="sort" label="" width="70"></el-table-column>
                    <el-table-column prop="tags" label="" width="100"></el-table-column>
                    <!-- <el-table-column class="tag" label="" :label-width="formLabelWidth">
                        <span @click="toggleTag(item.value)" :class="{'active': item.value == form.tags}" v-for="(item, index) in tags">{{item.name}}</span>
                    </el-table-column> -->
                    <el-table-column width="80" label="">
                        <template scope="scope">
                            <el-tag v-if="scope.row.status == 0" type="success"></el-tag>
                            <el-tag v-else type="danger"> </el-tag>
                        </template>
                    </el-table-column>
                    <el-table-column prop="addate" label="" width="180"></el-table-column>
                    <el-table-column prop="operate" label="" width="150" fixed="right">
                        <template scope="scope">
                            <el-button @click="edit(scope.row)"
                                type="text" size="small">
                            </el-button>
                            <el-button @click="offline(scope.$index, scope.row)" type="text" size="small">
                                <i>{{ scope.row.status == 1 ? ' ' : ' ' }}</i>
                            </el-button>
                            <el-button @click="del(scope.$index, scope.row)" type="text" size="small"></el-button>
                            <!-- <el-button v-if="scope.row.subject_num > 0" @click="$router.push({name:'course-manage-course-answer-analysis', params:{id:scope.row.id}})"
                                type="text" size="small">
                            </el-button> -->
                        </template>
                    </el-table-column>
                </el-table>
                <div class="block">
                    <el-pagination
                            @current-change="sectionPageChange"
                            :current-page="section.page"
                            :page-size="section.pagesize"
                            small
                            layout="total,prev, pager, next"
                            :total="section.total">
                    </el-pagination>
                </div>
            </div>
        </section>
    </article>
</template>
<script>
    import courseService from '../../../services/course/courseService'
    import CourseCategoryTree from '../../component/tree/CourseCategory.vue'
    import CourseCategorySelect from '../../component/select/CourseCategory.vue'
    import SectionCategoryMenu from '../../component/select/SectionCategoryMenu.vue'
    import ImagEcropperInput from '../../component/upload/ImagEcropperInputSec.vue'
    import DateRange from '../../component/form/DateRangePicker.vue'

    export default {
        components: {
            CourseCategoryTree,SectionCategoryMenu,ImagEcropperInput,DateRange,CourseCategorySelect
        },
        data () {
            return {
                ratio: 0, // 
                category: {
                    currentData: {},
                    loading: false,
                    title: '',
                    data: [],
                },
                // 
                formLabelWidth: '50px', // label
                editPlacehoder: '',
                rules: {
                    name: [
                        {
                            required: true,
                            message: '',
                            trigger: 'blur'
                        }
                    ]
                },
                section: {
                    loading: false,
                    data: [],
                    stime :'',
                    etime:'',
                    page: 1,
                    pagesize: 10,
                    total: 0,
                    gov_id: void 0, // id
                    category_id: void 0, // id
                    course_name: '',
                    type: '',
                    level: void 0,
                    create_start: void 0,
                    create_end: void 0,
                    need_testing: void 0, //  01
                    status:'', // 2-  1- 0-
                },
                defaultProps: {
                    children: 'children',
                    label: 'name'
                },
                SecMenu:[],
                Mult:'true',//  ,
                treeData: [],
                ended:void 0,
            }
        },
        watch: {
            '$store.state.index.secMenu'(){
                this.category.currentData = Object.assign({},this.$store.state.index.secMenu) //vuex 
            },
            'category.currentData.id'(){
                this.fetchCourseLists () 
                // this.$refs.secCategory.handleNodeClick()
            },
        //    '$store.state.index.examCate'(){
        //        this.fetchCourseLists () 
        //        this.fetchData()
        //     }        
        },
        activated () {
            this.category.currentData.id = ''
            this.category.loading = true
            // this.fetchData()
            this.fetchCourseLists()
        },
        // computed: {
        //     examCateid( ){
        //         return this.$store.state.index.examCate //Vue examCate
        //     }
        // },
        methods: {
            edit(row){
                console.log(row)
                if(row.category_name==""||row.category_name==""||row.category_name==""){
                    this.$router.push({name: 'course-manage-addCourse-herbal', params: {herbalInfo: row,handle:'edit'}, query: {id: row.contentid}})
                }
                else{
                    this.$router.push({name: 'course-manage-addCourse', params: {courseInfo: row,handle:'edit'}, query: {id: row.contentid}})
                    
                }
                
            },
            // 
            treeNodeClick (type, data, node, store) {
                // console.log('===========   node.data.data==========  ')
                // console.log(type, data, node, store)
                // console.log(node.data.id)
                console.log("category_type============="+node.data)
                console.log(node.data)
                if (type == 1) { 
                    this.section.category_id=node.data.id
                    this.section.category_type=node.data.category_type
                    this.section.category_name=node.data.name
                    this.ended=node.data.ended
                    // console.log(this.section.category_type)
                    // if (this.nodeSelected && this.nodeSelected.value === data.value) return  
                    this.fetchCourseLists()
                    this.nodeParentSelected = node.parent// 
                    this.nodeSelected = node // 
                    this.fetchParam = Object.assign({},node.data)  //
                } else if (type == 2) {
                    this.moveToNode = node
                }
            },
            //   01
            offline(index, row) {
                console.log(row)
                let txt = row.status == 0 ? '' : ''
                let finalStatus = row.status == 0 ? 1 : 0
                xmview.showDialog(`${txt} <span style="color:red">${row.course_name}</span> ?`, () => {
                    courseService.offlineCourse({
                        id: row.contentid,
                        status: finalStatus
                    }).then((ret) => {
                        row.status = finalStatus
                    })
                })
            },
            // fetchData() {// 4-23
            //     let param={
            //             category_id: this.examCateid , // 3- 
            //             page: 1,
            //             chapter_type:1,
            //             pagesize: -1,
            //         }
            //     courseService.fetchChapterCategory( param).then((ret) => {
            //             this.SecMenu=ret
            //             // console.log('this.SecMenu+++++++',param,this.SecMenu)
            //             xmview.setContentLoading(false)     
            //         })
            // },
            fetchCourseLists () { //
                this.section.loading = true
                let obj = Object.assign({},this.section)
                if(obj.status === ''){
                    obj.status = -1
                }
                return courseService.getPublicCourselist(obj).then((ret) => {
                    xmview.setContentLoading(false)
                    this.section.data = ret.data
                    this.section.total = ret._exts.total
                    this.section.loading = false
                })
            },
              // 
            del(index, row) {
                xmview.showDialog(` <span style="color:red">${row.course_name}</span> ?`, () => {
                    courseService.deleteCourse({
                        course_id: row.contentid
                    }).then(() => {
                        xmview.showTip('success', '')
                        this.data.splice(index, 1)
                    })
                })
            },

            //
            addCourse(){
                console.log(this.section.category_type)
            //  this.$router.push({ name:'course-manage-addCourse'}
                if(this.ended==1){
                    if( this.section.category_type==3|| this.section.category_type==4||this.section.category_type==5){
                        this.$router.push({ name:'course-manage-addCourse-herbal',params:{herbalInfo:this.section,handle:'add'}})
                        return 
                    }
                    else{
                        this.$router.push({ name:'course-manage-addCourse',params:{addcourseInfo:this.section,handle:'add'}})
                        return
                    }
                }
                xmview.showTip('error','')
                return
            },
           //
            addHerbal(){
            console.log(this.section.category_type)
            //  this.$router.push({ name:'course-manage-addCourse'}
                if(this.ended==1){
                    if( this.section.category_type==3|| this.section.category_type==4||this.section.category_type==5){
                        this.$router.push({ name:'course-manage-addCourse-herbal',params:{herbalInfo:this.section,handle:'add'}})
                        return 
                    }
                    else{
                        this.$router.push({ name:'course-manage-addCourse',params:{courseInfo:this.section,handle:'add'}})
                        return
                    }
                }
                xmview.showTip('error','')
                return
            },
            update (index, row) {
                this.$router.push({
                    name:'exam-course-edit',
                    params:{
                        id:row.id,
                        courseInfo:row,
                    }
                })
            },
            sectionPageChange (val) {
                this.section.page = val
                this.fetchCourseLists()
            }
        }
    }
</script>



